import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Grommet, Box, Button, Heading, Layer} from 'grommet';
import YouTube from 'react-youtube';
import HeaderBar from './HeaderBar';
import {FormNextLink} from 'grommet-icons';
import {withCookies, Cookies} from 'react-cookie';
import {instanceOf} from 'prop-types'

const theme = {
    global: {
      colors: {
        brand: '#3F51B5'
      },
      font: {
        family: "'-apple-system', 'BlinkMacSystemFont', 'Roboto'",
        size: '16px',
        height: '20px',
      },
    },
  };

class Video extends Component{
    static propTypes = {
        cookies: instanceOf(Cookies).isRequired
    };

    constructor(props){
        super(props);

        const { cookies } = props;

        this.state = {
            watched: cookies.get('watched') || false,
            clickNotEnd: false
        };
    }

    saveWatched() {
        const { cookies } = this.props;

        cookies.set('watched', true, {path: '/'});
        this.setState({watched: true})
    }

    render(){
        return(
            <Grommet full theme={theme}>
                <Box fill>
                <HeaderBar/>
                <Box direction='row' flex overflow={{ horizontal: 'hidden' }}>
                <Box flex align='center' justify='center'>
                    <Box flex align='center' justify='center'>
                    <Heading level="4" alignSelf="start">교육 자료 영상</Heading>
                       <YouTube videoId={"D1PvIWdJ8xo"} opts={{width: "1024", height: "576"}} onEnd={() => this.saveWatched()}/>
                    {
                        this.state.watched?
                        <Button icon={<FormNextLink/>} label="Next" margin="small" alignSelf="end" onClick={() => window.location.href = '/quiz'}/>:
                        <Button icon={<FormNextLink/>} label="Next" margin="small" alignSelf="end" onClick={() => this.setState({clickNotEnd: true})} color="white-4"/>
                    }
                    </Box>
                    {this.state.clickNotEnd && (
                        <Layer
                        onEsc={() => this.setState({clickNotEnd: false})}
                        onClickOutside={() => this.setState({clickNotEnd: false})}
                      >
                          <Box pad="medium" gap="small" width="medium">
                          <Heading level="4" margin="none">
                        교육 자료 영상을 끝까지 시청해주세요.
                      </Heading>
                          <Box
                        as="footer"
                        gap="small"
                        direction="row"
                        align="center"
                        justify="end"
                        pad={{ top: "medium", bottom: "small" }}
                      >
                        <Button label="close" onClick={() => this.setState({clickNotEnd: false})} />
                        </Box>
                        </Box>
                      </Layer>
                    )}
                </Box>
                </Box>
                </Box>
            </Grommet>
        );
    }
}

export default withCookies(withRouter(Video))